import ApplicationMinimalBase from "jx/projecttypes/ApplicationMinimalBase";
import Importer from "jx/projecttypes/editorformattype2/Importer";
import Exporter from "jx/projecttypes/editorformattype2/Exporter";
export default class ApplicationMinimal extends ApplicationMinimalBase {
    constructor(dataObject) {
        super(dataObject);
    }
    _create() {
        console.log("ApplicationMinimal create");
        super._create();
        this.info.assetsDir = "build/assets";
        this.importer = new Importer(this.jx, this.info);
        this.exporter = new Exporter(this.jx, { projectInfo: this.info });
        this.exporter.config.exportFile = "config.json";
    }
}
